.about {
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.695);
    padding-top: 10%;
  }
  
  .parent-box {
    width: 50%;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.616);
    margin-top: 5%;
    border-radius: 22px;
    margin-left: 25%;
    position: relative; /* add position relative */
    z-index: 1; /* add z-index */
  }
  
  h1 {user-select: none;
    padding-top: 3%;
    color: #ffff;
    padding-bottom: 50px;
    position: relative; /* add position relative */
    z-index: 2; /* set a higher z-index value */
  }
  
  p {user-select: none;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    color: aliceblue;
    margin: 5%;
    padding-bottom: 50px;
    position: relative; /* add position relative */
    z-index: 2; /* set a higher z-index value */
  }
  
  .parent-box {
    overflow: hidden;
  }
  
  .parent-box p {
    animation: scroll-text 200s linear infinite;
    transition: 1s;
    animation-delay: 0.33s;
  }
  
  .parent-box h1 {
    animation: scroll-text 60s linear infinite;
    transition: 1s;
    animation-delay: 0.33s;
  }
  
  @keyframes scroll-text {
    0% {
      transform: translateY(10%);
    }
    100% {
      transform: translateY(-900%);
    }
  }
  /* Styles for screens smaller than 768px */
@media only screen and (max-width: 767px) {
  .parent-box {
    width: 80%; /* decrease width of parent-box */
    margin-left: 10%; /* adjust margin */
  }
  
  h1 {
    font-size: 2rem; /* decrease font size */
    padding-bottom: 30px; /* adjust padding */
  }
  
  p {
    font-size: 1.2rem; /* decrease font size */
    padding-bottom: 30px; /* adjust padding */
  }
}
