*{
    top: 0;
    margin: 0;
}
body {
  margin: 0;
  padding: 0;
}

.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #000000;
    z-index: 999;
    transition: background-color 0.5s ease-in-out;
  }
  .navbar.scrolled {
    background-color: #000000;
  }
  .navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    padding: 0 20px;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .navbar-logo {
    
    font-size: 1.5rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #d6caca;
    text-decoration: none;
  }
  .navbar-logo :hover {
    
    font-size: 1.5rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #d6caca;
    text-decoration: none;
  }
  
  .navbar-toggle {
    display: none;
    cursor: pointer;
  }
  
  .navbar-toggle span {
    display: block;
    width: 30px;
    height: 3px;
    margin-bottom: 5px;
    background-color: #d6caca;
    transition: transform 0.3s ease-in-out;
  }
  
  .navbar-menu {
    display: flex;
    justify-content: flex-end;
    list-style: none;
  }
  
  .navbar-item {
    font-size: 1.5rem;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #d6caca;
    text-decoration: none;
    color: #ffffff;
    margin:  20px;
    
  }
  
  .navbar-menu a {
    font-size: 1rem;
    color: #fff0f0;
    text-decoration: none;
    text-transform: uppercase;
  }
  
  @media (max-width: 768px) {
    .navbar-toggle {
      display: block;
    }
  
    .navbar-menu {
      position: absolute;
      top: 80px;
      left: 0;
      width: 100%;
      height: calc(100vh - 80px);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.3s ease-in-out;
    }
  
    .navbar-menu.open {
      opacity: 1;
      pointer-events: all;
    }
  
    .navbar-menu a {
      color: #000;
      font-size: 2rem;
      margin: 20px 0;
    }
  
    .navbar-item {
      margin: 0;
    }
  
    .open span:first-child {
      transform: rotate(45deg) translate(7px, 7px);
    }
  
    .open span:nth-child(2) {
      opacity: 0;
    }
  
    .open span:last-child {
      transform: rotate(-45deg) translate(7px, -7px);
    }
  }
  
  
  
  .navbar-logo:hover {
    position: relative;
    animation: neon-glow 1s ease-in-out infinite alternate;
  }
  
  @keyframes neon-glow {
    0% {
      text-shadow: 0 0 10px #03febb, 0 0 20px #03febb, 0 0 30px #03febb, 0 0 40px #03febb, 0 0 50px #03febb, 0 0 60px #03febb, 0 0 70px #03febb;
    }
    100% {
      text-shadow: 0 0 5px #03febb, 0 0 15px #03febb, 0 0 25px #03febb, 0 0 35px #03febb, 0 0 45px #03febb, 0 0 55px #03febb, 0 0 65px #03febb;
    }
  }
  
  @keyframes flicker {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0.8;
    }
  }
  .navbar-menu a:hover {
    animation: neon-glow 1s infinite;
  }
  
  @keyframes neon-glow {
    0% {
      opacity: 0.5;
    }
    5% {
      opacity: 1;
    }
    10% {
      opacity: 0.5;
    }
    15% {
      opacity: 1;
    }
    20% {
      opacity: 0.5;
    }
    25% {
      opacity: 1;
    }
    30% {
      opacity: 0.5;
    }
    35% {
      opacity: 1;
    }
    40% {
      opacity: 0.5;
    }
    45% {
      opacity: 1;
    }
    50% {
      opacity: 0.8;
    }
    55% {
      opacity: 1;
    }
    60% {
      opacity: 0.8;
    }
    65% {
      opacity: 1;
    }
    70% {
      opacity: 0.8;
    }
    75% {
      opacity: 1;
    }
    80% {
      opacity: 0.8;
    }
    85% {
      opacity: 1;
    }
    90% {
      opacity: 0.8;
    }
    95% {
      opacity: 1;
    }
    100% {
      opacity: 0.5;
    }
  }
  