.parent{
    background-color: rgba(0, 0, 0, 0.811);
    height: 100%;
    padding-bottom:20px ;
    margin-top: 80PX;
    
}
.question-container {
    margin: 20px 0;
    width: 50%;
    margin-left: 24%;
   
  }
  
  .question-text {
    color: aliceblue;
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .answer-input {
    color: aliceblue;
    background-color: rgba(0, 0, 0, 0.385);
    width: 80%;
    height: 30px;
    padding: 5px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 3px;
    transition: border-color 0.2s ease-in-out;
  }
  
  .answer-input:focus {
    outline: none;
    border-color: #4CAF50;
  }
  
  .submit-button {
    align-items: center;
    align-content: center;
    justify-items: center;
    width: 10%;
    margin-left: 45%;
    display: block;
    padding: 5 25px;
    height: 30px;
    margin-top: 10px;
   
    background-color: #4CAF50;
    color: white;
    font-size: 20px;
    font-weight: 900;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .submit-button:hover {
    background-color: #3e8e41;
  }
  
  


  @media (max-width: 375px) {
  .submit-button {
    align-items: center;
    align-content: center;
    justify-items: center;
    width: 40%;
    margin-left: 29%;
    display: block;
    padding: 5 25px;
    height: 30px;
    margin-top: 10px;
    background-color: #4CAF50;
    color: white;
    font-size: 20px;
    font-weight: 900;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
   
}
