}
}



@media only screen and (max-width: 360px) {
  .question-container {
    width: 80%;
    margin-left: 10%;
  }
  .submit-button {
    width: 50%;
    margin-left: 25%;
  }
}
/* CSS for screens smaller than 1440px (Samsung Galaxy S20 Ultra) */
@media only screen and (max-width: 1440px) {
  .question-container {
    width: 60%;
    margin-left: 20%;
  }
  
  .answer-input {
    width: 70%;
  }
  
  .submit-button {
    width: 20%;
    margin-left: 40%;
    font-size: 16px;
  }
}
