.logo {
    
   z-index: -1;
   position: fixed;
    justify-content: center;
    align-items: center;
   
   
   
  }
  
  .retro-style {
    user-select: none;
    font-family: 'Inconsolata', monospace;
    font-family: 'Inconsolata', monospace;
font-family: 'Jost', sans-serif;
    text-transform: uppercase;
    font-size: 6rem;
    color: #fcfcfc;
    text-shadow: 0 0 10px #0fdcb0;
    animation: neon-glow 1s infinite;
  }
  
  @keyframes neon-glow {
    0% {
      opacity: 0.5;
    }
    5% {
      opacity: 1;
    }
    10% {
      opacity: 0.5;
    }
    15% {
      opacity: 1;
    }
    20% {
      opacity: 0.5;
    }
    25% {
      opacity: 1;
    }
    30% {
      opacity: 0.5;
    }
    35% {
      opacity: 1;
    }
    40% {
      opacity: 0.5;
    }
    45% {
      opacity: 1;
    }
    50% {
      opacity: 0.8;
    }
    55% {
      opacity: 1;
    }
    60% {
      opacity: 0.8;
    }
    65% {
      opacity: 1;
    }
    70% {
      opacity: 0.8;
    }
    75% {
      opacity: 1;
    }
    80% {
      opacity: 0.8;
    }
    85% {
      opacity: 1;
    }
    90% {
      opacity: 0.8;
    }
    95% {
      opacity: 1;
    }
    100% {
      opacity: 0.5;
    }
  }

  @import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200&family=Jost:ital@1&display=swap');
