body {
  margin: 0;
  padding: 0;
  
}


video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.video-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  transform: scale(1.1);
  overflow: hidden;
}
.video-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
}

@media (max-width: 768px) {
  .video-wrapper {
    width: 100%;
    height: auto;
    transform: scale(3.5);
  }
}


.retro-style {
  user-select: none;
  font-family: 'Inconsolata', monospace;
  font-family: 'Inconsolata', monospace;
  font-family: 'Jost', sans-serif;
  text-transform: uppercase;
  font-size: 6rem;
  color: #fcfcfc;
  text-shadow: 0 0 10px #03febb;
  animation: neon-glow 1s infinite;
  z-index: 1; /* Ensure the text is on top of the video */
  position: fixed; /* Position the text in the center */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes neon-glow {
  0% {
    opacity: 0.5;
  }
  5% {
    opacity: 1;
  }
  10% {
    opacity: 0.5;
  }
  15% {
    opacity: 1;
  }
  20% {
    opacity: 0.5;
  }
  25% {
    opacity: 1;
  }
  30% {
    opacity: 0.5;
  }
  35% {
    opacity: 1;
  }
  40% {
    opacity: 0.5;
  }
  45% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
  55% {
    opacity: 1;
  }
  60% {
    opacity: 0.8;
  }
  65% {
    opacity: 1;
  }
  70% {
    opacity: 0.8;
  }
  75% {
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  85% {
    opacity: 1;
  }
  90% {
    opacity: 0.8;
  }
  95% {
    opacity: 1;
  }
  100% {
    opacity: 0.5;
  }
}

@import url('https://fonts.googleapis.com/css2?family=Inconsolata:wght@200&family=Jost:ital@1&display=swap');
